@keyframes glass-overflow-in {

	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

@keyframes glass-overflow-out {

	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}

@keyframes pulse {

	0% {

		opacity: 1;
	}

	100% {
	
		opacity: .3;
	}
}

@keyframes mfp-bg-animation-in {

	0% {

		opacity: 0;
	}

	100% {

		opacity: 0.8;
	}
}
